<template>
  <div class="boom-wrap">
    <canvas></canvas>
    <div id="clouds">
      <div class="cloud x1"></div>
      <div class="cloud x2"></div>
      <div class="cloud x3"></div>
      <div class="cloud x4"></div>
      <div class="cloud x5"></div>
    </div>
  </div>
</template>

<script>
import Fret from "./fret.js";
import Bobo from "./bubble.js";
export default {
  data() {
    return {};
  },
  created() {
    this.$nextTick(() => {
      let bobo = new Bobo();
      let fret = new Fret();

      fret.start();
      bobo.animate();
    });
  },
  beforeDestroy() {},
  methods: {}
};
</script>

<style>
@import url("./clouds.css");
</style>